import { ref } from 'vue'

export function stringHelper() {
  // set a reference in the function
  const stringValue = ref('')

  // set a reference to the function that updates the name
  function truncateFileName(leftClip, rightClip, stringVal) {
    stringValue.value = `${stringVal.substring(0, leftClip)}...${stringVal.substring((stringVal.length - rightClip), stringVal.length)}`
  }

  return {
    stringValue,
    truncateFileName
  }
}