<template>
  <v-card class="flex-column file-item">
    <v-card-text :data-active="active" @dragenter.prevent="setActive" @dragover.prevent="setActive" @dragleave.prevent="setInactive" @drop.prevent="onDrop">
      <v-row class="d-flex flex-column" dense align="center" justify="center" :dropZoneActive="active">
        <v-icon class="attach-ico" size="60" v-if="!fileUploaded">mdi-attachment</v-icon>
        <v-card-text class="item-ico" size="60" v-else-if="fileUploaded">.siff</v-card-text>
        <h2 class="item-title-text">
          {{cardTitle}}
        </h2>
        <p>
          Status Text
        </p>
        <input id="fileUpload" type="file" hidden>
        <v-btn color="black" class="item-btn" @click="selectFile()">
          {{buttonText}}
        </v-btn>
      </v-row>
    </v-card-text>
  </v-card>
</template>

<script setup>
import { ref, onMounted, onUnmounted } from 'vue'
import { stringHelper } from '@/lib/helperFunctions'

const emit = defineEmits(['files-dropped'])
const {stringValue, truncateFileName} = stringHelper()

let active = ref(false)
let currentColor = ref('black')
let inActiveTimeout = null
let fileUploaded = ref(false)
let buttonText = ref('Search for File')
let cardTitle = ref('Drag and Drop')

const props = defineProps(['fileObj'])

// setActive and setInactive use timeouts, so that when you drag an item over a child element,
// the dragleave event that is fired won't cause a flicker. A few ms should be plenty of
// time to wait for the next dragenter event to clear the timeout and set it back to active.
function setActive() {
	active.value = true
	clearTimeout(inActiveTimeout)
}

function setInactive() {
	inActiveTimeout = setTimeout(() => {
		active.value = false
	}, 50)
}

function onDrop(e) {
  props.fileObj.fileName = e.dataTransfer.files[0].name
  props.fileObj.file = [...e.dataTransfer.files]
	emit('file-components', props.fileObj)
  truncateFileName(5, 3, e.dataTransfer.files[0].name)

  buttonText.value = 'Change File'
  fileUploaded.value = true
  cardTitle.value = stringValue.value

  var reader = new FileReader()
  reader.readAsText(e.dataTransfer.files[0], "UTF-8")

  reader.onload = function (evt) {
    // Send the file to the Web Worker
    this.$fileWorker.postMessage(e.dataTransfer.files[0].name, evt.target.result, '/app/site/hosting/restlet.nl?script=64&deploy=1')

    // Listen for messages from the Web Worker
    this.$fileWorker.addEventListener('message', event => {
      const { response, error } = event.data

      if (response) {
        console.log('File uploaded successfully:', response)
      } else if (error) {
        console.error('Error uploading file:', error)
      }
    })
  }
}

function preventDefaults(e) {
	e.preventDefault()
}

function selectFile() {
  document.getElementById("fileUpload").click()
}


const events = ['dragenter', 'dragover', 'dragleave', 'drop']

onMounted(() => {
	events.forEach((eventName) => {
		document.body.addEventListener(eventName, preventDefaults)
	})

  document.getElementById("fileUpload").addEventListener('change', (e) => {
    e.dataTransfer = {
      files: e.target.files
    }
    onDrop(e)
  })
})

onUnmounted(() => {
	events.forEach((eventName) => {
		document.body.removeEventListener(eventName, preventDefaults)
	})
})

</script>

