    transform: scaleX(-1) scaleY(-1);
  }

  .item-ico {
    border-radius: settings.$border-radius-root;
    width: 100px !important;
    height: 100px !important;
    color: white;
    background-color: #2dba87;
    font-size: 16pt;
    font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: center;
  }  

  .file-item {
    width: 200px;
    border-radius: settings.$border-radius-root;
    padding: 20px;
  }
  .file-item .item-title-text {
    margin: 3vh 3vh 1vh 3vh;
