<template>
  <v-card class="action-block">
    <v-row class="d-flex flex-column" dense align="center" justify="center">
      <v-btn class="action-button plus-button" @click="handleAddButton"><v-icon class="attach-ico">mdi-plus</v-icon></v-btn>
      <span>Add Files</span>
    </v-row>
    <v-divider class="divider" color="white" thickness="2px" />
    <v-row class="d-flex flex-column" dense align="center" justify="center">
      <v-btn class="action-button next-button"><v-icon class="attach-ico">mdi-arrow-right</v-icon></v-btn>
      <span>Begin Upload</span>
    </v-row>
  </v-card>
</template>

<script>
export default {
  name: 'ActionBlock',
  props: {
    fileObjs: {
      type: Array,
      required: true,
    },
  },
  methods: {
    handleAddButton () {
      this.fileObjs.push({
        id: this.fileObjs.length,
        fileLoaded: false,
        fileAdded: false,
        fileName: null,
        file: null
      })
    }
  }
}
</script>

